@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
body {
  margin: 0;
  padding: 0; }

.app-container {
  height: 100vh; }
  .app-container:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(to bottom right, #002f4b, #dc4225);
    opacity: .6; }

.navigation {
  width: 100%;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9999; }
  .navigation ul {
    min-width: 768px;
    list-style: none;
    text-align: center;
    padding: 0;
    margin: 0; }
  .navigation li {
    display: inline; }

.navigation_link {
  display: inline-block;
  text-align: center;
  text-decoration: none;
  padding: 20px 20px 5px 20px;
  font-size: 20px;
  font-family: "Open Sans";
  color: white; }
  .navigation_link:hover {
    border-bottom: 1px solid yellow;
    color: mediumblue;
    transition: color .3s ease-in-out; }

.home-container {
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; }

.home-container__support-container {
  max-width: 1024px;
  margin: 0 auto; }

.home-container__name {
  text-align: center;
  width: 100%;
  font-size: 80px;
  cursor: default;
  color: white;
  padding-top: 10px;
  font-family: "Open Sans"; }

.home-container__info {
  text-align: center;
  font-size: 22px;
  cursor: default;
  color: white;
  font-family: "Open Sans";
  padding-top: 10px; }

.home-container__social {
  width: 100%; }
  .home-container__social ul {
    list-style: none;
    text-align: center;
    padding: 0;
    margin: 0; }
  .home-container__social li {
    display: inline; }
  .home-container__social a {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    padding: 30px 20px 5px 20px; }
  .home-container__social img {
    height: 32px;
    width: 32px; }

